<template>
  <div>
    <el-container class="me-area">
      <el-main class="me-main">
        <pre>
      </pre>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'Log',
  data () {
    return {}
  },
  computed: {
    title () {
      return '日志 - For Fun'
    }
  }
}
</script>

<style scoped>
  .el-container {
    width: 700px;
  }

  .me-main {
    overflow: hidden;
  }

  .me-log-box {
    margin-left: 30%;
    margin-top: 20px;
  }

</style>



